*{
    margin: 0;
    padding: 0;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.nav{
    display: flex;
    justify-content: space-between;
    padding: 20px;
    margin: 0 20px;
    align-items: center;
}
.change{
    display: flex;

}
.text{
    font-size: x-large;
    color: #363636;
}
.Btn{
    margin: 0 10px;
}
.lng{
    padding: 8px;
    border: 1px solid;
    border-radius: 7px;
}


table {
    width: 80%;
    border-collapse: collapse;
    margin-bottom: 20px;
    margin:auto;
    margin-top: 10px;
  }
 
  th, td {
    padding: 8px;
    text-align: left;
  }
  th {
    background-color: #68a5ff30;
    color: #075ad7;
  }
  th span{
    font-size: small;
    color: #809bcd;
  }
  tr:hover {
    background-color: #f5f5f5;
  }
  tbody tr td:first-child{
    color: #075ad7;
  }


  .option_select{
    width: 20%;
  }